.checkboxWrapper {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.customCheckbox {
  margin: 0 10px 0 0;
  position: relative;
  width: 40px;
  height: 20px;
  appearance: none;
  background: #c6c6c6;
  outline: none;
  border-radius: 20px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}
.customCheckbox:checked {
  background: #ff6e6f;
}
.customCheckbox:before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  top: 0;
  left: 0;
  background: #fff;
  transform: scale(1.1);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}
.customCheckbox:checked::before {
  left: 20px;
}
.customCheckboxLabel {
  font-size: 18px;
}
