.likeButton {
  transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  filter: grayscale(100%);
  cursor: pointer;
  line-height: 0;
  margin-right: 20px;
}
.likeButton.liked {
  color: #ff6e6f;
  filter: grayscale(0);
}
.likeButton:hover {
  border-color: currentColor;
}

.likeIcon {
  width: 22px;
  height: 20px;
  display: inline-block;
  position: relative;
  font-size: 1.5rem;
  background: url("data:image/svg+xml;base64,")
    no-repeat center;
  background-size: 100%;
  animation: heartUnlike 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
}

.liked .likeIcon {
  animation: heartPulse 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
}
.liked .likeIcon .heartAnimationFirst,
.liked .likeIcon .heartAnimationSecond {
  background: url("data:image/svg+xml;base64,")
    no-repeat center;
  background-size: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 16px;
  height: 14px;
  opacity: 0;
}
.liked .likeIcon .heartAnimationFirst::before,
.liked .likeIcon .heartAnimationFirst::after,
.liked .likeIcon .heartAnimationSecond::before,
.liked .likeIcon .heartAnimationSecond::after {
  content: "";
  background: inherit;
  background-size: 100%;
  width: inherit;
  height: inherit;
  display: inherit;
  position: relative;
  top: inherit;
  left: inherit;
  opacity: 0;
}
.liked .likeIcon .heartAnimationFirst {
  animation: heartFloatMain-1 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
}
.liked .likeIcon .heartAnimationFirst::before,
.liked .likeIcon .heartAnimationFirst::after {
  width: 12px;
  height: 10px;
  visibility: hidden;
}
.liked .likeIcon .heartAnimationFirst::before {
  opacity: 0.6;
  animation: heartFloatSub-1 1s 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275)
    both;
}
.liked .likeIcon .heartAnimationFirst::after {
  animation: heartFloatSub-2 1s 0.15s cubic-bezier(0.175, 0.885, 0.32, 1.275)
    both;
  opacity: 0.75;
}
.liked .likeIcon .heartAnimationSecond {
  animation: heartFloatMain-2 1s 0.1s cubic-bezier(0.175, 0.885, 0.32, 1.275)
    both;
}
.liked .likeIcon .heartAnimationSecond::before,
.liked .likeIcon .heartAnimationSecond::after {
  width: 10px;
  height: 8px;
  visibility: hidden;
}
.liked .likeIcon .heartAnimationSecond::before {
  animation: heartFloatSub-3 1s 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275)
    both;
  opacity: 0.25;
}
.liked .likeIcon .heartAnimationSecond::after {
  animation: heartFloatSub-4 1s 0.15s cubic-bezier(0.175, 0.885, 0.32, 1.275)
    both;
  opacity: 0.4;
}

@keyframes heartPulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
}
@keyframes heartUnlike {
  50% {
    transform: scale(0.75);
  }
}
@keyframes heartFloatMain-1 {
  0% {
    opacity: 0;
    transform: translate(0) rotate(0);
  }
  50% {
    opacity: 1;
    transform: translate(0, -25px) rotate(-20deg);
  }
}
@keyframes heartFloatMain-2 {
  0% {
    opacity: 0;
    transform: translate(0) rotate(0) scale(0);
  }
  50% {
    opacity: 0.9;
    transform: translate(-10px, -38px) rotate(25deg) scale(1);
  }
}
@keyframes heartFloatSub-1 {
  0% {
    visibility: hidden;
    transform: translate(0) rotate(0);
  }
  50% {
    visibility: visible;
    transform: translate(13px, -13px) rotate(30deg);
  }
}
@keyframes heartFloatSub-2 {
  0% {
    visibility: hidden;
    transform: translate(0) rotate(0);
  }
  50% {
    visibility: visible;
    transform: translate(18px, -10px) rotate(55deg);
  }
}
@keyframes heartFloatSub-3 {
  0% {
    visibility: hidden;
    transform: translate(0) rotate(0);
  }
  50% {
    visibility: visible;
    transform: translate(-10px, -10px) rotate(-40deg);
  }
  100% {
    transform: translate(-50px, 0);
  }
}
@keyframes heartFloatSub-4 {
  0% {
    visibility: hidden;
    transform: translate(0) rotate(0);
  }
  50% {
    visibility: visible;
    transform: translate(2px, -18px) rotate(-25deg);
  }
}
